<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-12">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Mantenimientos</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Visualizacion y Registro</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-md-4">
                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'distrito' }]">
                            <div class="card-body">
                                <div class="d-flex">
                                    <div class="row" @click="selectModule('distrito')" style="cursor: pointer;">
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'distrito' }">Distritos
                                            Judiciales</a>
                                        <span class="text-secondary"
                                            :class="{ 'text-light': selectedModule === 'distrito' }">Lista y
                                            registro</span>
                                    </div>
                                    <button class="btn px-3 rounded ml-auto"
                                        :class="{ 'btn-light text-primary': selectedModule === 'distrito', 'btn-outline-secondary text-dark': selectedModule !== 'distrito' }"
                                        @click="mostrarModalJudiciales" :disabled="disabledIEspecialidad">
                                        <i data-feather="plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
    
                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'instancias' }]">
                            <div class="card-body">
                                <div class="d-flex">
                                    <div class="row" @click="selectModule('instancias')" style="cursor: pointer;">
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'instancias' }">Instancias</a>
                                        <span class="text-secondary"
                                            :class="{ 'text-light': selectedModule === 'instancias' }">Lista y registro</span>
                                    </div>
                                    <button class="btn px-3 rounded ml-auto"
                                        :class="{ 'btn-light text-primary': selectedModule === 'instancias', 'btn-outline-secondary text-dark': selectedModule !== 'instancias' }"
                                        @click="mostrarModalInstancia" :disabled="disabledInstancia">
                                        <i data-feather="plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'especialidad' }]">
                            <div class="card-body">
                                <div class="d-flex">
                                    <div class="row" @click="selectModule('especialidad')" style="cursor: pointer;">
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'especialidad' }">Especialidades</a>
                                        <span class="text-secondary"
                                            :class="{ 'text-light': selectedModule === 'especialidad' }">Lista y registro</span>
                                    </div>
                                    <button class="btn px-3 rounded ml-auto"
                                        :class="{ 'btn-light text-primary': selectedModule === 'especialidad', 'btn-outline-secondary text-dark': selectedModule !== 'especialidad' }"
                                        @click="mostrarModalEspecialidad" :disabled="disabledDistrito">
                                        <i data-feather="plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div class="col-md-8" style="overflow-y: auto; max-height: 465px;">
                    <distrito v-if="selectedModule === 'distrito'" ref="judicialesComponent" />
                    <especialidad v-else-if="selectedModule === 'especialidad'" ref="especialidadComponent" />
                    <instancias v-else-if="selectedModule === 'instancias'" ref="instanciasComponent" />

                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import feather from 'feather-icons';

import distrito from './Distrito-Judicial/distrito.vue';
import instancias from './Distrito-Judicial/instancia.vue';
import especialidad from './Distrito-Judicial/especialidad.vue';

export default {
    name: "mantenimiento",
    components: {
        distrito,
        instancias,
        especialidad
    },

    created() {
        // Obtener el mdulo seleccionado desde el almacenamiento local
        // const savedModuleDistrito= localStorage.getItem('selectedModule');

        // Establecer el mdulo seleccionado o utilizar el predeterminado
        this.selectModule('distrito');
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            selectedModule: null,
            disabledDistrito: true,
            disabledInstancia: true,
            disabledIEspecialidad: true
        };
    },

    methods: {
        mostrarModalEspecialidad() {
            const especialidadComponent = this.$refs.especialidadComponent;
            especialidadComponent.mostrarModal();
        },

        mostrarModalInstancia() {
            const instanciasComponent = this.$refs.instanciasComponent;
            instanciasComponent.mostrarModal();
        },

        mostrarModalJudiciales() {
            const judicialesComponent = this.$refs.judicialesComponent;
            judicialesComponent.mostrarModal();
        },

        buttonDisabled(module) {
            if (module === 'especialidad') {
                this.disabledDistrito = false;
                this.disabledInstancia = true;
                this.disabledIEspecialidad = true;
            } else if (module === 'instancias') {
                this.disabledDistrito = true;
                this.disabledInstancia = false;
                this.disabledIEspecialidad = true;
            } else if (module === 'distrito') {
                this.disabledDistrito = true;
                this.disabledInstancia = true;
                this.disabledIEspecialidad = false;
            }
        },

        selectModule(module) {
            this.selectedModule = module;

            // Guardar el mdulo seleccionado en el almacenamiento local
            localStorage.setItem('selectedModule', module);

            this.buttonDisabled(this.selectedModule);
        },
    },
};
</script>